.input-radio + label::before {
  content: "";
  position: absolute;
  left: -46px;
  top: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cecece;
  border-radius: 50%;
}
.input-radio + label::after {
  display: none;
  content: "";
  position: absolute;
  left: -37px;
  top: 9px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #68b738;
}
.input-radio:checked + label::after {
  display: block;
}
@media (min-width: 768px) {
  .input-radio + label::before {
    left: -59px;
    top: 4px;
    width: 40px;
    height: 40px;
  }
  .input-radio + label::after {
    left: -48px;
    top: 15px;
    width: 18px;
    height: 18px;
  }
}
.input-checkbox + label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 2px;
  width: 30px;
  height: 30px;
  border: 1px solid #cecece;
}
.input-checkbox + label::after {
  display: none;
  content: "";
  position: absolute;
  left: 4px;
  top: 8px;
  width: 23px;
  height: 20px;
  background-image: url("../img/tick.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.input-checkbox:checked + label::after {
  display: block;
}
@media (min-width: 768px) {
  .input-checkbox + label::before {
    left: 0;
    top: -2px;
    width: 40px;
    height: 40px;
  }
  .input-checkbox + label::after {
    left: 9px;
    top: 9px;
  }
}
